import React from "react";
import "../styles/App.css";
import { Link, Redirect } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

class SignUp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            email: "",
            password: "",
            confirmPassword: "",
            redirect: null,
            type: "Customer",
        };
    }
    render() {
        if (this.state.redirect != null) {
            return <Redirect push to={"/" + this.state.redirect} />;
        }
        return (
            <div
                className="dark"
                style={{
                    height: "100%",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    flexDirection: "column",
                }}
            >
                <p style={{ fontSize: 40 }}>Create an account</p>
                <Form>
                    <Form.Group style={{ width: 500 }}>
                        <Form.Control
                            type="text"
                            placeholder="Name"
                            onChange={(e) =>
                                this.setState({ name: e.target.value })
                            }
                            style={{ marginBottom: 20 }}
                        />
                        <Form.Control
                            type="text"
                            placeholder="Email"
                            onChange={(e) => {
                                this.setState({ email: e.target.value });
                            }}
                            style={{ marginBottom: 20 }}
                        />
                        <Form.Control
                            type="password"
                            placeholder="Password"
                            onChange={(e) => {
                                this.setState({ password: e.target.value });
                                this.setWarning(
                                    this.state.email,
                                    e.target.value,
                                    this.state.confirmPassword
                                );
                            }}
                            style={{ marginBottom: 20 }}
                        />
                        <Form.Control
                            type="password"
                            placeholder="Confirm password"
                            onChange={(e) => {
                                this.setState({
                                    confirmPassword: e.target.value,
                                });
                                this.setWarning(
                                    this.state.email,
                                    this.state.password,
                                    e.target.value
                                );
                            }}
                            style={{ marginBottom: 20 }}
                        />
                        <Form.Control
                            as="select"
                            onChange={(e) => {
                                this.setState({
                                    type: e.target.value,
                                });
                            }}
                            style={{ marginBottom: 20 }}
                        >
                            <option>Customer</option>
                            <option>Merchant</option>
                        </Form.Control>
                    </Form.Group>
                </Form>
                <p style={{ height: 19, color: "#c72e2e" }}>
                    {this.state.warning}
                </p>
                <Button
                    variant="outline-dark"
                    style={{ marginTop: 20, marginBottom: 20 }}
                    onClick={() => this.signUp()}
                >
                    Sign up
                </Button>
                <Link to={"/signIn"} className="clear-button">
                    Sign in instead
                </Link>
            </div>
        );
    }

    setWarning(email, password, confirmPassword) {
        if (password.length < 5) {
            this.setState({
                warning: "Password must be 5 characters or longer",
            });
        } else if (password !== confirmPassword) {
            this.setState({ warning: "Passwords do not match" });
        } else {
            this.setState({ warning: "" });
        }
    }

    async signUp() {
        if (this.state.warning === "") {
            let success = await this.props.signUp(
                this.state.email,
                this.state.password,
                this.state.name,
                this.state.type
            );
            if (success) {
                if (this.state.type === "Customer") {
                    await this.setState({ warning: "" });
                    this.setState({ redirect: "store" });
                } else {
                    window.location.replace(
                        "https://docs.google.com/spreadsheets/d/1OmSHVwKSGLl7FlZ8CesF-PmSvCaJcgd8Q02F6bMDoVc/edit#gid=1080396694"
                    );
                }
            }
        }
    }
}

export default SignUp;
