import React from "react";
import "../styles/App.css";

import { Link } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";

class Cart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            purchases: [],
            products: [],
            total: 0,
        };
    }

    async componentWillMount() {
        await this.fetchProducts();
    }

    async fetchProducts() {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({ text: "" });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        let allProducts = null;

        await fetch(
            "http://localhost:8000/get-all-details",
            requestOptions
        )
            .then((response) => response.json())
            .then((result) => {
                allProducts = result;
            })
            .catch((error) => console.log("error", error));

        this.setState({ products: allProducts });

        let allPurchases = ["Bagel", "Mocha"];
        this.setState({ purchases: allPurchases });
    }

    render() {
        if (this.state.products.length === 0) {
            return (
                <div style={{ ...styles.container, fontSize: 30 }}>
                    Looks like you don't have any items in your cart
                </div>
            );
        }
        return (
            <div style={{ ...styles.container, fontSize: 30 }}>
                <div style={{ display: "flex", flexDirection: "row" }}>
                    {this.getProducts()}
                </div>
                <Link to={"/store"} className="basic">
                    <Button
                        variant="outline-dark"
                        style={{ ...styles.buttonStyle, marginTop: 20 }}
                    >
                        Purchase
                    </Button>
                </Link>
            </div>
        );
    }

    getProducts() {
        let products = this.state.products;
        let categoryProducts = [];

        for (let purchase of this.state.purchases) {
            products.forEach((product) => {
                if (product.packageIdentifiers[0].type === purchase) {
                    categoryProducts.push(
                        <Card
                            className="basic"
                            key={product.itemId.itemCode}
                            style={styles.cardBody}
                            onClick={() => {
                                let productKey = product.itemId.itemCode;
                                this.handleShow(productKey);
                            }}
                        >
                            {this.getImage(product.packageIdentifiers[0].type)}
                            <Card.Body
                                style={{
                                    display: "flex",
                                    flexDirection: "column",
                                    justifyContent: "center",
                                }}
                            >
                                <Card.Title>
                                    <h2>
                                        {product.packageIdentifiers[0].type}
                                    </h2>
                                </Card.Title>
                                <Card.Subtitle style={{ marginTop: 5 }}>
                                    ${product.packageIdentifiers[0].value}
                                </Card.Subtitle>
                            </Card.Body>
                        </Card>
                    );
                }
            });
        }
        return categoryProducts;
    }

    getImage(product) {
        switch (product) {
            case "Americano":
                return (
                    <Card.Img
                        src={require("../images/americano.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cappuccino":
                return (
                    <Card.Img
                        src={require("../images/cappuccino.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Mocha":
                return (
                    <Card.Img
                        src={require("../images/mocha.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Green Tea":
                return (
                    <Card.Img
                        src={require("../images/greentea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Black Tea":
                return (
                    <Card.Img
                        src={require("../images/blacktea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Jasmine Tea":
                return (
                    <Card.Img
                        src={require("../images/jasminetea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Brownie":
                return (
                    <Card.Img
                        src={require("../images/brownie.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cookie":
                return (
                    <Card.Img
                        src={require("../images/cookie.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cake Pop":
                return (
                    <Card.Img
                        src={require("../images/cakepop.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Bagel":
                return (
                    <Card.Img
                        src={require("../images/bagel.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Muffin":
                return (
                    <Card.Img
                        src={require("../images/muffin.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Croissant":
                return (
                    <Card.Img
                        src={require("../images/croissant.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            default:
                return (
                    <Card.Img
                        src={require("../images/breakfast.png")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
        }
    }
}

const styles = {
    container: {
        height: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
    },
    cardBody: {
        width: 300,
        margin: "20px",
    },
    homeIcon: {
        height: "3em",
        width: "3em",
        position: "absolute",
        left: 10,
        top: 0,
        marginTop: "0.5em",
        marginRight: "0.5em",
    },
};

export default Cart;
