import React from "react";
import "./styles/App.css";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { Link } from "react-router-dom";
import Welcome from "./components/Welcome";
import SignUp from "./components/SignUp";
import SignIn from "./components/SignIn";
import Home from "./components/Home";
import Cart from "./components/Cart";
import Category from "./components/Category";
import Error from "./components/Error";
import Messages from "./components/Messages";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            categories: [],
            products: [],
            serverRoute: "/",
            shouldUpdate: true,
        };
    }

    componentWillUnmount() {
        this.setState({ shouldUpdate: false });
    }

    componentDidMount() {
        this.fetchCategories();
    }

    render() {
        return (
            <>
                <Router>
                    <div style={{ height: "100vh" }}>
                        <Navbar bg="dark" variant="dark" fixed="top">
                            <Link
                                to={
                                    localStorage.getItem("auth")
                                        ? "/store"
                                        : "/"
                                }
                            >
                                <Navbar.Brand>Tradot</Navbar.Brand>
                            </Link>
                            <Nav className="ml-auto">
                                {localStorage.getItem("auth") ? (
                                    <Link to={"/cart"}>
                                        <Navbar.Text
                                            className="mr-sm-2"
                                            style={{ color: "white" }}
                                        >
                                            Cart
                                        </Navbar.Text>
                                    </Link>
                                ) : (
                                    <></>
                                )}
                            </Nav>
                        </Navbar>
                        <Switch>
                            <Route exact path="/">
                                <Welcome />
                            </Route>
                            <Route path="/signUp">
                                <SignUp
                                    signUp={(
                                        username,
                                        password,
                                        firstName,
                                        lastName
                                    ) =>
                                        this.signUp(
                                            username,
                                            password,
                                            firstName,
                                            lastName
                                        )
                                    }
                                />
                            </Route>
                            <Route path="/signIn">
                                <SignIn
                                    signIn={(username, password) =>
                                        this.signIn(username, password)
                                    }
                                />
                            </Route>
                            {this.getUserRoutes()}
                            <Route path="/">
                                <Error />
                            </Route>
                        </Switch>
                    </div>
                </Router>

                <div
                    style={{
                        display: "flex",
                        position: "fixed",
                        right: 0,
                        bottom: 0,
                    }}
                >
                    {localStorage.getItem("auth") ? <Messages /> : <></>}
                </div>
            </>
        );
    }

    async delay(delayInms) {
        return new Promise((resolve) => {
            setTimeout(() => {
                resolve(2);
            }, delayInms);
        });
    }

    async fetchCategories() {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({ text: "What do you sell?" });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        let categories = [];

        await fetch(
            "http://localhost:8000/get-all-categories",
            requestOptions
        )
            .then((response) => response.text())
            .then((result) => {
                categories = JSON.parse(result);
            })
            .catch((error) => console.log("error", error));

        this.setState({ categories });
    }

    async getToken(email, password) {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({ email: email, password: password });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        fetch(
            "http://localhost:8000/api/auth",
            requestOptions
        )
            .then((response) => response.json())
            .then((result) => {
                localStorage.setItem("name", result.user.name);
                this.forceUpdate();
            })
            .catch((error) => console.log("error", error));

        localStorage.setItem("username", email);
        localStorage.setItem("password", password);
        this.forceUpdate();
        return true;
    }

    async signIn(username, password) {
        let success = await this.getToken(username, password);
        localStorage.setItem("auth", true);

        return success;
    }

    async signUp(email, password, name, type) {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        myHeaders.append("Access-Control-Allow-Origin", "*");

        var raw = JSON.stringify({
            name: name,
            email: email,
            password: password,
            type: type,
        });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        try {
            fetch(
                "http://localhost:8000/api/users",
                requestOptions
            )
                .then((response) => response.text())
                .then((result) => console.log(result));
        } catch (error) {
            console.log(error);
        }

        localStorage.setItem("name", name);
        localStorage.setItem("username", email);
        localStorage.setItem("password", password);
        this.forceUpdate();

        return true;
    }

    getUserRoutes() {
        let username = localStorage.getItem("username");

        if (username != null) {
            let routes = [];

            routes.push(
                <Route key={"route_user"} exact path={"/store"}>
                    <Home
                        username={username}
                        categories={this.state.categories}
                    />
                </Route>
            );
            routes.push(
                <Route key={"route_user_cart"} path={"/cart"}>
                    <Cart username={username} />
                </Route>
            );

            for (const category of this.state.categories) {
                routes.push(
                    <Route
                        key={"route_user_" + category}
                        exact
                        path={"/store/" + category}
                    >
                        <Category name={category} username={username} />
                    </Route>
                );
            }

            return routes;
        }
    }
}

export default App;
