import React from "react";
import { ButtonGroup, Button, Card, Modal } from "react-bootstrap";

import "../styles/App.css";

class Category extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            quantity: 1,
            category: "",
            product: "",
            products: [],
            productKey: -1,
            show: false,
            map: new Map(),
        };
    }

    async componentWillMount() {
        await this.fetchProducts();
    }

    async componentDidMount() {
        let url = window.location.href;
        let preProccessed = url.split("/")[4];
        let processed =
            preProccessed.charAt(0).toUpperCase() + preProccessed.slice(1);
        this.state.category = processed;
    }

    async fetchProducts() {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({ text: "" });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        let allProducts = null;

        await fetch(
            "http://localhost:8000/get-all-details",
            requestOptions
        )
            .then((response) => response.json())
            .then((result) => {
                allProducts = result;
            })
            .catch((error) => console.log("error", error));

        this.setState({ products: allProducts });
    }

    handleClose = () => this.setState({ show: false });
    handleShow = (key) => this.setState({ show: true, productKey: key });

    handleSubmit = () => {
        this.setState({ product: this.state.map.get(this.state.productKey) });
        this.setState({ show: false });

        // name = product
        // quantity = quantity
    };

    render() {
        return (
            <>
                <div
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                    }}
                >
                    <div style={styles.pageTitle}>
                        <span>
                            <h1>
                                {this.props.name[0].toUpperCase() +
                                    this.props.name.substring(
                                        1,
                                        this.props.name.length
                                    )}
                            </h1>
                        </span>
                    </div>
                    <div style={styles.pageBody}>{this.getProducts()}</div>
                </div>
                <Modal
                    show={this.state.show}
                    onHide={this.handleClose}
                    style={{ marginTop: "20vh" }}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add to Cart</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        Woohoo, you're adding{" "}
                        {this.state.map.get(this.state.productKey)} to your
                        cart! Select quantity:
                        {this.getQuantityButtons()}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Cancel
                        </Button>
                        <Button variant="primary" onClick={this.handleSubmit}>
                            Submit
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }

    async delay(delayInms) {
        return new Promise((resolve) => {
            setTimeout(() => {
                resolve(2);
            }, delayInms);
        });
    }

    getProducts() {
        let products = this.state.products;
        let categoryProducts = [];

        products.forEach((product) => {
            if (product.departmentId === this.state.category) {
                this.state.map.set(
                    product.itemId.itemCode,
                    product.packageIdentifiers[0].type
                );
                categoryProducts.push(
                    <Card
                        className="basic"
                        key={product.itemId.itemCode}
                        style={styles.cardBody}
                        onClick={() => {
                            let productKey = product.itemId.itemCode;
                            this.handleShow(productKey);
                        }}
                    >
                        {this.getImage(product.packageIdentifiers[0].type)}
                        <Card.Body
                            style={{
                                display: "flex",
                                flexDirection: "column",
                                justifyContent: "center",
                            }}
                        >
                            <Card.Title>
                                <h2>{product.packageIdentifiers[0].type}</h2>
                            </Card.Title>
                            <Card.Subtitle style={{ marginTop: 5 }}>
                                ${product.packageIdentifiers[0].value}
                            </Card.Subtitle>
                        </Card.Body>
                    </Card>
                );
            }
        });
        return categoryProducts;
    }

    getImage(product) {
        switch (product) {
            case "Americano":
                return (
                    <Card.Img
                        src={require("../images/americano.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cappuccino":
                return (
                    <Card.Img
                        src={require("../images/cappuccino.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Mocha":
                return (
                    <Card.Img
                        src={require("../images/mocha.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Green Tea":
                return (
                    <Card.Img
                        src={require("../images/greentea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Black Tea":
                return (
                    <Card.Img
                        src={require("../images/blacktea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Jasmine Tea":
                return (
                    <Card.Img
                        src={require("../images/jasminetea.jpg")}
                        style={{ height: 375 }}
                        alt="Card image"
                    />
                );
            case "Brownie":
                return (
                    <Card.Img
                        src={require("../images/brownie.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cookie":
                return (
                    <Card.Img
                        src={require("../images/cookie.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Cake Pop":
                return (
                    <Card.Img
                        src={require("../images/cakepop.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Bagel":
                return (
                    <Card.Img
                        src={require("../images/bagel.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Muffin":
                return (
                    <Card.Img
                        src={require("../images/muffin.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            case "Croissant":
                return (
                    <Card.Img
                        src={require("../images/croissant.jpg")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
            default:
                return (
                    <Card.Img
                        src={require("../images/breakfast.png")}
                        style={{ height: 475 }}
                        alt="Card image"
                    />
                );
        }
    }

    getQuantityButtons() {
        return (
            <ButtonGroup
                style={{ display: "flex", flexDirection: "row", marginTop: 30 }}
            >
                <Button
                    variant="outline-dark"
                    onClick={() => this.decrease()}
                    style={{ width: 40 }}
                >
                    -
                </Button>
                <Button variant="outline-dark" disabled>
                    {this.state.quantity}
                </Button>
                <Button
                    variant="outline-dark"
                    onClick={() => this.increase()}
                    style={{ width: 40 }}
                >
                    +
                </Button>
            </ButtonGroup>
        );
    }

    decrease() {
        if (this.state.quantity > 1) {
            this.setState({ quantity: this.state.quantity - 1 });
        }
    }

    increase() {
        this.setState({ quantity: this.state.quantity + 1 });
    }
}

const styles = {
    pageTitle: {
        height: "20%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "row",
        fontWeight: "bold",
        textDecoration: "underline",
        marginTop: "10vh",
    },
    pageBody: {
        height: "40%",
        width: "70%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "row",
        flexWrap: "wrap",
    },
    cardBody: {
        textDecoration: "none",
        color: "#000",
        width: 350,
        height: "75vh",
        margin: "20px",
    },
};

export default Category;
