import React from "react";
// import Lottie from "react-lottie";
import { Link } from "react-router-dom";
import Card from "react-bootstrap/Card";

import "../styles/App.css";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: false,
        };
    }

    render() {
        return (
            <div
                style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center",
                }}
            >
                <div style={styles.pageTitle}>
                    <span style={{ marginTop: 100 }}>
                        <h1 style={{ textDecoration: "underline" }}>
                            Welcome to our Coffee Shop,{" "}
                            {localStorage.getItem("name")}
                        </h1>
                    </span>
                </div>
                <div style={styles.pageBody}>{this.getCategories()}</div>
            </div>
        );
    }

    getCategories() {
        let categories = [];

        for (const category of this.props.categories) {
            categories.push(
                <Link
                    to={"/store/" + category}
                    key={category}
                    className="basic"
                    style={styles.link}
                >
                    <Card style={styles.cardBody} className="text-center">
                        {this.getImage(category)}
                        <Card.Body
                            style={{
                                display: "flex",
                                flexDirection: "column",
                                justifyContent: "center",
                            }}
                        >
                            <Card.Title>
                                <h2>
                                    {category[0].toUpperCase() +
                                        category.substring(1, category.length)}
                                </h2>
                            </Card.Title>
                        </Card.Body>
                    </Card>
                </Link>
            );
        }

        return categories;
    }

    getImage(category) {
        switch (category) {
            case "Coffee":
                return (
                    <Card.Img
                        src={require("../images/coffee.jpg")}
                        style={{ height: 550 }}
                        alt="Card image"
                    />
                );
            case "Tea":
                return (
                    <Card.Img
                        src={require("../images/tea.png")}
                        style={{ height: 550 }}
                        alt="Card image"
                    />
                );
            case "Sweets":
                return (
                    <Card.Img
                        src={require("../images/sweets.png")}
                        style={{ height: 550 }}
                        alt="Card image"
                    />
                );
            case "Breakfast":
                return (
                    <Card.Img
                        src={require("../images/breakfast.png")}
                        style={{ height: 550 }}
                        alt="Card image"
                    />
                );
            default:
                return (
                    <Card.Img
                        src={require("../images/breakfast.png")}
                        alt="Card image"
                    />
                );
        }
    }
}

const styles = {
    pageTitle: {
        height: "20%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "row",
        fontWeight: "bold",
    },
    pageBody: {
        height: "40%",
        width: "90%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "row",
        flexWrap: "wrap",
    },
    cardBody: {
        textDecoration: "none",
        color: "#000",
        width: 350,
        height: "75vh",
    },
    link: {
        margin: "1em",
    },
    shoppingCartIcon: {
        height: "3em",
        width: "3em",
        position: "absolute",
        right: 0,
        top: 0,
        marginTop: "0.5em",
        marginRight: "0.5em",
    },
    swithButton: {
        marginTop: "0.5em",
        marginLeft: "0.5em",
    },
};

export default Home;
